import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import { BrowserRouter } from 'react-router-dom'; //HashRouter  Hash  /#/ ,   , #    X

//redux  (  )
import {Provider} from 'react-redux';
import { combineReducers, createStore } from 'redux';
import { type } from '@testing-library/user-event/dist/type';

//   state + reducer 
let alertBase = true;

function reducer2(state = alertBase, action) {
  if(action.type === 'alertClose') {
  return state = false;
  } else {
  return state
  }
}

let defaultState = [
  {id:0, name : 'White and Black', quan: 0}
  // ,{id:1, name: ' ', quan: 1}
]; //state 


// redux state     (state  )-Cart
//   reducer!! createStore()  ,       return
// reducer   state !  !
function reducer(state = defaultState, action) {
  if( action.type === '') {

      // state  id : .  ?
      // findIndex : Array    js
      //    (a),  array   a  === ???  ?
      //        
      let found = state.findIndex((a)=>{ return a.id === action.payload.id});

      //     ,      
      if( found >= 0 ) {
        let copy = [...state];
        copy[found].quan++;

      } else {
      let copy = [...state];
      // dispatch      action.payload
      // reducer   dispatch()   object
      copy.push(action.payload);
      return copy
      }

   //defaultState default parameter 
    } else if ( action.type === ''){
    let copy = [...state];
    copy[action.payload].quan++;
    return copy

  } if ( action.type === ''){
    let copy = [...state];
    if (copy[action.payload].quan > 0)
    copy[action.payload].quan--;
    return copy
  }

   else {
    return state
  }
}
//   . if    state ,     state




// Redux  ?  props    !
// reducer   combineReducers({}) (import )
let store = createStore(combineReducers({reducer, reducer2}));


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
    <Provider store={store}>
    <App />
    </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
