import React, { useEffect, memo } from "react";
import { Table } from "react-bootstrap";
import { connect, useDispatch, useSelector } from "react-redux";

function Cart(props) {

    // 1.    
    // 2. (css - transform )


    //     ' index.js store  state '  

    // useSelector()
    // redux  state  !  props   
    // useSelector()   redux state     
    //         store 
    let state = useSelector((state) => state);
    //   useSelector((state) => state.reducer);   state.reducer  
    // console.log(state.reducer)

    // useDispatch()
    // dispatch   
    //  props   dispatch  ( onClick)
    let dispatch = useDispatch();

    return(
        <div>
        <Table>
            <thead>
                <tr>
                    <th>ID</th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
        <tbody>
            {
                state.reducer.map((a,i)=>{
                    return (
                        <tr key={i}>
                            <td>{a.id}</td>
                            <td>{a.name}</td>
                            <td>{a.quan}</td>

                            {/*    */}
                            {/* Redux  2 */}
                            {/* reudcer   (ex.)  props.dispatch({type:'-'})  */}
                            {/* dispatch()         dispatch({type:'', payload: }) */}
                            {/* ({type ~~~}) action ,     !!(index) */}
                            <td><button onClick={()=> { dispatch({ type : '', payload : a.id })}}>+</button></td>
                            <td><button onClick={()=> { dispatch({ type : '', payload : a.id })}}>-</button></td>
                        </tr>
                    )
                })
            }
                    {/* <tr>
                    <td>{props.state[0].name}</td> */}
                     {/* index.js  ''    */}
                    {/* <td>name</td>
                    <td></td>
                    <td>Table cell</td>
                    </tr> */}
                </tbody>
            </Table>
            { props.alertOpen === true?
            <div className="my-alert2">
                <p>  50% !!</p>

                {/*     -  xxx */}
                {/* index.js     dispatch */}
                {/* <button onClick={()=>{
                    props.alertOpen(false)
                }}></button> */}

                {/*    redux store  , redux      */}
                <button onClick={()=>{
                    props.dispatch({type : 'alertClose'})}}>
                </button>
            </div> : null }
            {/* <Parent ="" ="22"/> */}
        </div>
    )
}

